}
.register_modal .input_container {
    /* flex-direction: row; */
}
.register_modal .input_container .input_wrap{
    margin-top: 16px;
    padding:12px 8px 8px 8px;
    line-height: 24px;
    width: 100%;
}
.register_modal .input_container label{
    flex-direction: row;
    border-color: rgb(47, 51, 54);
    border-width: 2px;
    border-style: solid;
    line-height: 62px;
    margin-bottom: 50px;
    border-radius: 5px;
    background-color: black;
}
.register_modal label.focus{
    border-color: rgb(29, 155, 240);
    border-width: 2px;
}
.register_modal label.error{
    border-color: red;
    border-width: 2px;
}
.register_modal label span.nCnt{
    display: none;
    position: absolute;
    right: 5px;
    top: 5px;
    line-height: 25px
}
.register_modal label.focus span.nCnt, .register_modal label.yesValue span.nCnt{
    display: block;
}
.register_modal label span.value{
    font-size: 17px;
    position: absolute;
    left: 8px;
    color: rgb(110, 118, 125);
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.register_modal label span.underInput {
    position: absolute;
    bottom: -25px;
    line-height: 20px;
    margin-left: 5px;
}
.register_modal label.error span.error{
    color:red;
}

.register_modal label span.show_pass{
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    color: rgb(29, 155, 240);
}
.register_modal label.focus span.value {
    color: rgb(29, 155, 240);
}
.register_modal label.error span.value {
    color: red;
}
.register_modal label.focus span.value, .register_modal label.yesValue span.value, .register_modal label span.introduce{
    font-size: 14px;
    height: 20px;
    line-height: 20px;
    top: 10px;
}

.register_modal label input{
    width: 100%;
}
.register_modal label.focus input, .register_modal label.yesValue input{

}

